import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import NotFound from './views/404'
import AboutUs from './views/aboutus'
import Historia from './views/History'
import ConductCode from './views/ConductCode'
import Management from './views/Management'
import Services from './views/Services'
import ServicesIntro from './views/ServiceIntro'
import Contact from './views/Contact'
import FinanceInformation from './views/FinanceInformation'
import ServicoNaoVida from './views/ServicosNaoVida'
import SeguroAutomovel from './views/Automovel'
import SeguroVida from './views/SeguroVida'
import BancaSeguro from './views/BancaSeguro'
import Glossario from './views/Glossario'
import Toursure from './views/Toursure'
import NProgress from 'nprogress'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  scrollBehavior (to, from, savedPosition) {
    return {x: 0, y: 0}
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '*',
      name: '404',
      component: NotFound
    },
    {
      path: '/aboutus',
      name: 'aboutus',
      component: AboutUs
    },
    {
      path: '/history',
      name: 'history',
      component: Historia
    },
    {
      path: '/conductcode',
      name: 'conductcode',
      component: ConductCode
    },
    {
      path: '/management',
      name: 'management',
      component: Management
    },
    {
      path: '/services',
      name: 'services',
      component: Services
    },
    {
      path: '/servicesintro',
      name: 'servicesintro',
      component: ServicesIntro
    },
    {
      path: '/serviconaovida',
      name: 'serviconaovida',
      component: ServicoNaoVida
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/financeinformation',
      name: 'financeinformation',
      component: FinanceInformation
    },
    {
      path: '/servicos-automovel',
      name: 'servicos-automovel',
      component: SeguroAutomovel
    },
    {
      path: '/segurosvida',
      name: 'segurosvida',
      component: SeguroVida
    },
    {
      path: '/banca-seguro',
      name: 'banca-seguro',
      component: BancaSeguro
    },
    {
      path: '/glossario',
      name: 'glossario',
      component: Glossario
    },
    {
      path: '/toursure',
      name: 'toursure',
      component: Toursure
    }
  ]
})

router.beforeResolve((to, from, next) => {
  // let active = window.localStorage.getItem('language')
  //
  // if (active) {
  //   let activeLanguage = JSON.parse(active)
  //   i18n.locale = activeLanguage.locale
  // }
  // If this isn't an initial page load.
  if (to.name) {
    // Start the route progress bar.
    NProgress.start()
  }
  next()
})

router.afterEach((to, from) => {
  NProgress.done()
})

export default router
