                    <ul class="nav navbar-nav lan-menu">
                        <li class="dropdown">
                            <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button">
                                <img :src="activeLanguage.flag" alt=""/>{{ activeLanguage.name }}<span
                                    class="caret"></span></a>
                            <ul class="dropdown-menu">
                                <li><a href="#" @click.prevent="changeLanguage('pt')"><img src="images/mz-flag.png"
                                                                                           alt=""/>PT</a></li>
                                <li><a href="#" @click.prevent="changeLanguage('en')"><img src="images/en.png" alt=""/>EN</a>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import menu from '@/translations/menu'

export default {
  name: 'g-menu',
  i18n: {
    messages: menu
  },
  data () {
    return {
      activeLanguage: {}
    }
  },
  mounted () {
    let active = window.localStorage.getItem('language')

    if (active) {
      this.activeLanguage = JSON.parse(active)
    } else {
      this.activeLanguage = {
        name: 'PT',
        flag: 'images/mz-flag.png',
        locale: 'pt'
      }
    }
  },
  methods: {
    changeLanguage (lang) {
      let active = ''

      this.$i18n.locale = lang

      if (lang === 'en') {
        active = {
          name: 'EN',
          flag: 'images/en.png',
          locale: 'en'
        }
      } else {
        active = {
          name: 'PT',
          flag: 'images/mz-flag.png',
          locale: 'pt'
        }
      }

      this.activeLanguage = active

      window.localStorage.setItem('language', JSON.stringify(active))

      this.$eventHub.$emit('lang-change', lang)
    }
  }
}
</script>

